<template>
    <div class="reviews-list-container">
        <button class="arrow-button left-arrow" :disabled="currentPage===1">
            <img class="arrow-image" src="@/assets/newImages/arrow_pager_left.png" @click="turnLeft">
        </button>
        <div class="reviews-list-block">
            <div class="reviews-list" :id="listId">
                <div class="reviews-page">
                    <div v-for="j in itemsPerCurrentPage" :key="j" class="review-block">
                    <Review 
                    :style="`width: ${pageWidth}px;`"
                    :review="reviews[(currentPage-1)*itemsPerPage+j-1]"
                    />
                    </div>
                </div>
            </div>
            <div class="page-indicator-block">
                <div v-for="i in pagesCount" :key="i" class="page-indicator" :class="{'active':(currentPage===i)}">
            </div>
            </div>
        </div>
        <button class="arrow-button right-arrow" @click="turnRight" :disabled="currentPage===pagesCount">
            <img class="arrow-image" src="@/assets/newImages/arrow_pager_right.png">
        </button>
    </div>
    </template>
    
    <script>
    import { defineComponent } from '@vue/composition-api'
    import Review from './Review'
    import localizeFilter from '../../../filters/localize.filter';
    export default defineComponent({
        data: ()=>({
            reviews: Array.from({length: 8}, (_, i) => i + 1).map(i=>({
                name: localizeFilter(`landing_liquidity_providing_reviews_section_review_${i}_name`),
                profession: localizeFilter(`landing_liquidity_providing_reviews_section_review_${i}_profession`),
                text: localizeFilter(`landing_liquidity_providing_reviews_section_review_${i}_text`),
                image: require(`@/assets/newImages/landingLiquidityProviding/reviewImage${i}.jpg`),
            })),
            
            
            /*[
            {name: 'Марина Визграновская',profession: 'Учитель', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Пожарный', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Бариста', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Менеджер', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Логист', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Врач', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Учитель', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            {name: 'Марина Визграновская',profession: 'Таксист', text: 'Все наши ресурсы имеют защиту высшего класса. Получить доступ к вашим финансовым операциям не может никто кроме вас.', image: require('@/assets/newImages/reviewImage.png')},
            ],*/
            currentPage: 1,
            mountDate: +Date.now(),
            pageWidth: Math.min(window.innerWidth-110, 350),
        }),
        components: {
            Review
        },
        computed: {
            itemsPerPage() {
                const windowWidth = window.innerWidth
                const availableWidth = Math.min(windowWidth ,1200) 
                return Math.floor(availableWidth / this.pageWidth)
            },
            itemsPerCurrentPage() {
                if (this.currentPage === this.pagesCount) {
                    if (this.reviews.length % this.itemsPerPage === 0) {
                        return this.itemsPerPage
                    } else {
                        return this.reviews.length % this.itemsPerPage
                    }
                } else {
                    return this.itemsPerPage
                }
            },
            pagesCount() {
                return Math.ceil((this.reviews.length) / this.itemsPerPage)
            },
            listId() {
                return `reviewsList-${this.mountDate}`
            }
        },
        methods: {
            turnLeft() {
                this.currentPage = Math.max(this.currentPage-1, 1)
            },
            turnRight() {
                this.currentPage = Math.min(this.currentPage+1, this.pagesCount)
            },
            // handleScroll() {
            //     const el = document.getElementById(this.listId)
            //     if (el) {
            //          const pageWidth = el.offsetWidth
            //          this.currentPage = Math.floor(el.scrollLeft / pageWidth) + 1
            //     }
            // }
        },
        setup() {
            
        },
        mounted() {
